import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles/index';
import { Avatar, Divider, Icon, List, ListItem, ListItemText, Paper, Typography } from '@material-ui/core';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { NavLink, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import * as Actions from './store/actions';

const styles = theme => ({
    listItem: {
        color: 'inherit!important',
        textDecoration: 'none!important',
        height: 40,
        width: 'calc(100% - 16px)',
        borderRadius: '0 20px 20px 0',
        paddingLeft: 24,
        paddingRight: 12,
        '&.active': {
            backgroundColor: theme.palette.secondary.main,
            color: theme.palette.secondary.contrastText + '!important',
            pointerEvents: 'none',
            '& .list-item-icon': {
                color: 'inherit'
            }
        }
    }
});

class StockSidebarContent extends Component {
    state = {
        symbol: "MSFT",
        selectedIndex: 1,
    };

    handleSymbol = (symbol, index) => {
        this.setState({ symbol: symbol, selectedIndex: index });
        this.props.action.setSymbol(symbol);
    };

    render() {
        const { classes, symbol } = this.props;
        return (
            <div className="p-16 lg:p-24 lg:pr-4">
                <Paper elevation={1} className="rounded-8">
                    <div className="p-24 flex items-center">
                        <Typography>Symbol</Typography>
                    </div>
                    <Divider />
                    <List component="nav">
                        <ListItem
                            button
                            selected={this.state.selectedIndex === 1}
                            className={classes.listItem}
                            onClick={event => this.handleSymbol('MSFT', 1)}
                        >
                            <Icon className="list-item-icon text-16" color="action">star</Icon>
                            <ListItemText className="truncate pr-0" primary="MSFT" disableTypography={true} />
                        </ListItem>

                        <ListItem
                            button
                            selected={this.state.selectedIndex === 2}
                            className={classes.listItem}
                            onClick={event => this.handleSymbol('AAPL', 2)}
                        >
                            <Icon className="list-item-icon text-16" color="action">star</Icon>
                            <ListItemText className="truncate pr-0" primary="AAPL" disableTypography={true} />
                        </ListItem>

                        <ListItem
                            button
                            selected={this.state.selectedIndex === 3}
                            className={classes.listItem}
                            onClick={event => this.handleSymbol('INTC', 3)}
                        >
                            <Icon className="list-item-icon text-16" color="action">star</Icon>
                            <ListItemText className="truncate pr-0" primary="INTC" disableTypography={true} />
                        </ListItem>

                        <ListItem
                            button
                            selected={this.state.selectedIndex === 4}
                            className={classes.listItem}
                            onClick={event => this.handleSymbol('LUV', 4)}
                        >
                            <Icon className="list-item-icon text-16" color="action">star</Icon>
                            <ListItemText className="truncate pr-0" primary="LUV" disableTypography={true} />
                        </ListItem>

                        <ListItem
                            button
                            selected={this.state.selectedIndex === 5}
                            className={classes.listItem}
                            onClick={event => this.handleSymbol('NFLX', 5)}
                        >
                            <Icon className="list-item-icon text-16" color="action">star</Icon>
                            <ListItemText className="truncate pr-0" primary="NFLX" disableTypography={true} />
                        </ListItem>

                        <ListItem
                            button
                            selected={this.state.selectedIndex === 6}
                            className={classes.listItem}
                            onClick={event => this.handleSymbol('INTC', 6)}
                        >
                            <Icon className="list-item-icon text-16" color="action">star</Icon>
                            <ListItemText className="truncate pr-0" primary="INTC" disableTypography={true} />
                        </ListItem>

                        <ListItem
                            button
                            selected={this.state.selectedIndex === 7}
                            className={classes.listItem}
                            onClick={event => this.handleSymbol('ORCL', 7)}
                        >
                            <Icon className="list-item-icon text-16" color="action">star</Icon>
                            <ListItemText className="truncate pr-0" primary="ORCL" disableTypography={true} />
                        </ListItem>

                        <ListItem
                            button
                            selected={this.state.selectedIndex === 8}
                            className={classes.listItem}
                            onClick={event => this.handleSymbol('CMCSA', 8)}
                        >
                            <Icon className="list-item-icon text-16" color="action">star</Icon>
                            <ListItemText className="truncate pr-0" primary="CMCSA" disableTypography={true} />
                        </ListItem>
                    </List>
                </Paper>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => (
    {
        action: bindActionCreators({ ...Actions }, dispatch)
    }
);

// function mapDispatchToProps(dispatch) {
//     return bindActionCreators({ Actions: { ...Actions } }, dispatch);
// }

function mapStateToProps({ contactsApp }) {
    return {
        symbol: contactsApp.contacts.symbol,
        user: contactsApp.user
    }
}

// StockSidebarContent.propTypes = {
//     didSymbolChanged: PropTypes.func
// };

export default withStyles(styles, { withTheme: true })(withRouter(connect(mapStateToProps, mapDispatchToProps)(StockSidebarContent)));
