import * as Actions from '../actions';
import _ from '@lodash';

const initialState = {
    entities: [],
    symbol: 'MSFT',
    searchText: '',
    selectedContactIds: [],
    routeParams: {},
    contactDialog: {
        type: 'new',
        props: {
            open: false
        },
        data: null
    }
};

const contactsReducer = function (state = initialState, action) {
    switch (action.type) {
        case Actions.GET_STOCK_DATA:
            {
                return {
                    ...state,
                    entities: action.payload,
                    routeParams: action.routeParams
                };
            }

        case Actions.SET_SYMBOL_NAME:
            {
                return {
                    ...state,
                    symbol: action.data
                };
            }

        default:
            {
                return state;
            }
    }
};

export default contactsReducer;
