import React, { Component } from 'react';
import { Line } from 'react-chartjs-2';
import ChartComponent from "react-chartjs-2";
import 'chartjs-chart-financial';
import { withStyles } from '@material-ui/core/styles';
import { Typography } from '@material-ui/core';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import * as Actions from './store/actions';
import classNames from 'classnames';

const styles = theme => ({
    mailList: {
        padding: 0
    },
    mailItem: {},
    avatar: {
        backgroundColor: theme.palette.primary[500]
    },
    labels: {}

});

class Ohlc extends Component {

    state = {
        selectedStockMenu: null
    };

    openSelectedContactMenu = (event) => {
        this.setState({ selectedStockMenu: event.currentTarget });
    };

    closeSelectedStockMenu = () => {
        this.setState({ selectedStockMenu: null });
    };

    getFilteredArray = (data) => {
        let oTemp = {};
        const chartData = [];
        if (data === undefined) return null;
        let dataTemp = data;

        for (var key in dataTemp) {
            oTemp = {
                "t": key,
                "o": parseFloat(dataTemp[key]["1. open"]),
                "c": parseFloat(dataTemp[key]["4. close"]),
                "h": parseFloat(dataTemp[key]["2. high"]),
                "l": parseFloat(dataTemp[key]["3. low"]),
                "v": parseFloat(dataTemp[key]["5. volume"]),
            };
            oTemp.range = (oTemp.h - oTemp.l) * 100;
            oTemp.bullish = oTemp.c > oTemp.o;
            if (oTemp.high > this.highest) { this.highest = oTemp.h; }
            chartData.push(oTemp)
        };
        return chartData;
    };

    // componentDidMount() {
    //     let symbol = this.props.symbol;
    //     if (!symbol) {
    //         let symbol = "MSFT";
    //     }
    //     console.log('---------------componentDidMount---------------------');
    //     console.log();
    //     console.log('------------------------------------');
    //     this.props.action.getStock('', symbol);
    // }

    componentWillReceiveProps(nextProps) {
        let symbol = "MSFT";
        if (nextProps) {
            symbol = nextProps.symbol;
            if (!symbol) {
                symbol = "MSFT";
            }
        }

        if (this.props.symbol !== nextProps.symbol) {
            this.props.action.getStock('', nextProps.symbol);
        }
    }

    // componentDidUpdate(prevProps) {
    //     this.getFilteredArray(this.props.contacts);
    // }

    render() {
        const { classes, contacts, symbol } = this.props;
        const data = this.getFilteredArray(contacts, symbol);
        const { selectedStockMenu } = this.state;

        const dataset = {
            datasets: [{
                label: symbol + " Chart",
                data: data,
                fractionalDigitsCount: 2,
            }]
        };

        const options = {
            tooltips: {
                position: 'nearest',
                mode: 'index',
            },
            maintainAspectRatio: false,
            responsive: true,
            scales: {
                xAxes: [{
                    ticks: {
                        display: false,
                        maxRotation: 100,
                        autoSkip: false,
                        maxTicksLimit: 20

                    }
                }]
            }
        };

        if (!data) {
            return (
                <div className="flex items-center justify-center h-full">
                    <Typography color="textSecondary" variant="h5">
                        No Data Found!
                    </Typography>
                </div>
            );
        }

        return (
            <div className="flex flex-col items-center w-full ">
                <h2>{symbol} - Chart</h2>

                <ChartComponent
                    {...this.props}
                    type="ohlc"
                    data={dataset}
                    options={options}
                    height={500}
                    width={1000}
                    redraw={true}
                    style={{ width: '100%', height: '100%' }}
                />
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => (
    {
        action: bindActionCreators({ ...Actions }, dispatch)
    }
);
// function mapDispatchToProps(dispatch) {
//     return bindActionCreators({
//         action: { ...Actions },
//     }, dispatch);
// }

function mapStateToProps({ contactsApp }) {
    return {
        contacts: contactsApp.contacts.entities,
        symbol: contactsApp.contacts.symbol,
    }
}

export default withStyles(styles, { withTheme: true })(withRouter(connect(mapStateToProps, mapDispatchToProps)(Ohlc)));
