import axios from 'axios/index';

export const GET_STOCK_DATA = 'GET STOCK DATA';
export const SET_SYMBOL_NAME = 'SET SYMBOL';

export function getStock(routeParams, p1) {
    if (p1 === undefined) { p1 = "MSFT"; }
    const URL = `https://www.alphavantage.co/query?function=TIME_SERIES_DAILY&symbol=${p1}&apikey=UXWGI2GF5MOSHIG4`;

    const request = axios.get(URL, {
        params: routeParams
    });

    return (dispatch) =>
        request.then((response) =>
            dispatch({
                type: GET_STOCK_DATA,
                payload: response.data['Time Series (Daily)'],
                routeParams
            })
        );
}

export function setSymbol(data) {
    return {
        type: SET_SYMBOL_NAME,
        data
    }
} 