import * as Actions from '../../actions/base/index';
import _ from '@lodash';
import BaseDefaultSettings from '@basecomponent/BaseDefaultSettings';
import BaseLayouts from '@basecomponent/components/BaseLayout/BaseLayouts';

const initialState = {
    defaults: _.merge({}, BaseDefaultSettings),
    current: _.merge({}, BaseDefaultSettings)
};

const settings = function (state = initialState, action) {
    switch (action.type) {
        case Actions.SET_SETTINGS:
            {
                return {
                    ...state,
                    current: _.merge({}, state.current, action.value && action.value.layout && action.value.layout.style ? { layout: { config: BaseLayouts[action.value.layout.style].defaults } } : {}, action.value)
                };
            }
        case Actions.SET_DEFAULT_SETTINGS:
            {
                return {
                    ...state,
                    defaults: _.merge({}, state.defaults, action.value),
                    current: _.merge({}, state.defaults, action.value && action.value.layout && action.value.layout.style ? { layout: { config: BaseLayouts[action.value.layout.style].defaults } } : {}, action.value)
                };
            }
        case Actions.RESET_DEFAULT_SETTINGS:
            {
                return {
                    ...state,
                    defaults: _.merge({}, state.defaults),
                    current: _.merge({}, state.defaults)
                };
            }
        default:
            {
                return state;
            }
    }
};

export default settings;
