import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { BasePageSimple } from '@basecomponent';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import * as Actions from './store/actions';
import Ohlc from './ohlc';
import StockHeader from './StockHeader';
import StockSidebarContent from './StockSidebarContent';
import _ from '@lodash';
import withReducer from 'store/withReducer';
import reducer from './store/reducers';

const styles = theme => ({
    addButton: {
        position: 'absolute',
        right: 12,
        bottom: 12,
        zIndex: 99
    }
});

class StockApp extends Component {

    componentDidMount() {
        this.props.getStock(this.props.match.params);
    }

    componentDidUpdate(prevProps, prevState) {
        if (!_.isEqual(this.props.location, prevProps.location)) {
            this.props.getStock(this.props.match.params);
        }
    }

    render() {
        const { classes, openNewContactDialog } = this.props;

        return (
            <React.Fragment>
                <BasePageSimple
                    classes={{
                        contentCardWrapper: "p-16 sm:p-24 pb-80",
                        leftSidebar: "w-256",
                        header: "min-h-42 h-42 sm:h-106 sm:min-h-106"
                    }}
                    header={
                        <StockHeader pageLayout={() => this.pageLayout} />
                    }
                    content={
                        <Ohlc />
                    }
                    leftSidebarContent={
                        <StockSidebarContent />
                    }
                    sidebarInner
                    onRef={instance => {
                        this.pageLayout = instance;
                    }}
                    innerScroll
                />

            </React.Fragment>
        )
    };
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        getStock: Actions.getStock,
    }, dispatch);
}

function mapStateToProps({ contactsApp }) {
    return {
        contacts: contactsApp.contacts.entities,
        selectedContactIds: contactsApp.contacts.selectedContactIds,
        user: contactsApp.user
    }
}

export default withReducer('contactsApp', reducer)(withStyles(styles, { withTheme: true })(withRouter(connect(mapStateToProps, mapDispatchToProps)(StockApp))));
